.color-picker-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px;
    background-color: #f4f4f9;
    font-family: Arial, sans-serif;
}

.left-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
}

.image-frame {
    width: 300px; /* Fixed width for image preview */
    height: 300px; /* Fixed height for image preview */
    border: 1px solid #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.image-frame img {
    max-width: 100%;
    max-height: 100%;
}

.choose-image-button {
    padding: 10px 20px;
    border: none;
    background-color: #007bff; /* Primary button color */
    color: white;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 10px;
}

.choose-image-button:hover {
    background-color: #0056b3; /* Darker shade on hover */
}

.right-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 20px;
}

.color-palette {
    display: grid;
    grid-template-columns: repeat(5, 1fr); /* 5 columns in palette */
    gap: 10px; /* Gap between color blocks */
    margin-top: 20px;
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.color-block {
    width: 50px; /* Fixed width for color blocks */
    height: 50px; /* Fixed height for color blocks */
    border: 1px solid #ddd;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.color-block.disabled {
    cursor: not-allowed;
    opacity: 0.3; /* Reduced opacity for disabled blocks */
}

.color-code {
    display: none; /* Initially hide color code */
}

.color-block:hover .color-code {
    display: block;
    position: absolute;
    bottom: -25px; /* Position relative to the block */
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
    color: #fff; /* Text color */
    padding: 5px; /* Padding around text */
    border-radius: 3px; /* Rounded corners */
    font-size: 12px; /* Font size */
    white-space: nowrap; /* Prevent wrapping */
    pointer-events: none; /* Allow click through */
    z-index: 1; /* Ensure above other elements */
}

.extract-colors-button {
    padding: 10px 20px;
    border: none;
    background-color: #28a745; /* Success button color */
    color: white;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 20px;
}

.extract-colors-button:hover {
    background-color: #218838; /* Darker shade on hover */
}

.pagination-controls {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.pagination-controls button {
    padding: 10px 20px;
    border: none;
    background-color: #007bff; /* Primary button color */
    color: white;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px; /* Space between buttons */
}

.pagination-controls button:disabled {
    background-color: #ccc; /* Disabled button color */
    cursor: not-allowed;
}

.pagination-controls button:hover:not(:disabled) {
    background-color: #0056b3; /* Darker shade on hover */
}
